@use 'base/variables' as v;

/** Media Queries **/
@mixin mobile {
    @media (min-width: #{v.$mobile}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{v.$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{v.$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton( $bgColor, $display ) {
    background-color: $bgColor;
    color: v.$white;
    font-weight: 700;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center;
    margin-top: calc(v.$separation / 2);
    display: $display;
    border: none;
    border-radius: .5rem;

    &:hover {
        background-color: darken($bgColor, 10);
        cursor: pointer;
    }
}
